.privacy-policy {
    min-height: 100vh;
    background: var(--bg-primary);
    padding: 2rem 0;
    margin-top: 80px; /* Account for fixed header */
}

.privacy-policy .container {
    max-width: 900px;
    margin: 0 auto;
    padding: 0 2rem;
}

/* Header Styles */
.privacy-policy__header {
    text-align: center;
    margin-bottom: 3rem;
}

.back-link {
    display: inline-flex;
    align-items: center;
    gap: 0.5rem;
    color: var(--text-secondary);
    text-decoration: none;
    margin-bottom: 2rem;
    padding: 0.75rem 1.5rem;
    border: 1px solid var(--border-color);
    border-radius: 50px;
    transition: all 0.3s ease;
    background: var(--card-bg);
}

.back-link:hover {
    color: var(--accent-primary);
    border-color: var(--accent-primary);
    transform: translateY(-2px);
}

.section-badge {
    display: inline-flex;
    align-items: center;
    gap: 0.5rem;
    padding: 0.5rem 1rem;
    background: var(--gradient-primary);
    border-radius: 50px;
    font-size: 0.9rem;
    font-weight: 500;
    color: white;
    margin-bottom: 1.5rem;
}

.page-title {
    font-size: 3rem;
    font-weight: 700;
    color: var(--text-primary);
    margin-bottom: 1rem;
    line-height: 1.2;
}

.title-gradient {
    background: var(--gradient-primary);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.page-description {
    font-size: 1.2rem;
    color: var(--text-secondary);
    max-width: 600px;
    margin: 0 auto;
    line-height: 1.6;
}

/* Content Styles */
.privacy-policy__content {
    display: flex;
    flex-direction: column;
    gap: 3rem;
}

.privacy-section {
    background: var(--card-bg);
    border-radius: 20px;
    padding: 2.5rem;
    border: 1px solid var(--card-border);
    box-shadow: var(--card-shadow);
    position: relative;
    overflow: hidden;
}

.privacy-section::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: var(--gradient-primary);
}

.section-icon {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 60px;
    height: 60px;
    background: var(--gradient-primary);
    border-radius: 15px;
    margin-bottom: 1.5rem;
    font-size: 1.5rem;
    color: white;
}

.privacy-section h2 {
    font-size: 2rem;
    font-weight: 600;
    color: var(--text-primary);
    margin-bottom: 1.5rem;
}

.app-section .app-header {
    background: rgba(139, 92, 246, 0.1);
    border: 1px solid rgba(139, 92, 246, 0.2);
    border-radius: 12px;
    padding: 1.5rem;
    margin-bottom: 2rem;
}

.app-header h3 {
    font-size: 1.5rem;
    font-weight: 600;
    color: var(--accent-primary);
    margin-bottom: 0.5rem;
}

.effective-date {
    font-weight: 500;
    color: var(--text-secondary);
    margin-bottom: 0.25rem;
}

.developer {
    font-weight: 500;
    color: var(--accent-primary);
}

.policy-content {
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

.policy-item h3,
.policy-item h4 {
    font-size: 1.3rem;
    font-weight: 600;
    color: var(--text-primary);
    margin-bottom: 1rem;
}

.policy-item p {
    color: var(--text-secondary);
    line-height: 1.7;
    margin-bottom: 1rem;
}

.policy-item ul {
    list-style: none;
    padding: 0;
    margin: 1rem 0;
}

.policy-item li {
    position: relative;
    padding-left: 1.5rem;
    color: var(--text-secondary);
    line-height: 1.6;
    margin-bottom: 0.5rem;
}

.policy-item li::before {
    content: '•';
    position: absolute;
    left: 0;
    color: var(--accent-primary);
    font-weight: bold;
    font-size: 1.2rem;
}

.policy-item strong {
    color: var(--text-primary);
    font-weight: 600;
}

/* Contact Section */
.contact-section .contact-info {
    text-align: center;
    margin-top: 1.5rem;
}

.contact-link {
    display: inline-flex;
    align-items: center;
    gap: 0.75rem;
    padding: 1rem 2rem;
    background: var(--gradient-primary);
    color: white;
    text-decoration: none;
    border-radius: 50px;
    font-weight: 500;
    transition: all 0.3s ease;
}

.contact-link:hover {
    transform: translateY(-2px);
    box-shadow: 0 10px 30px rgba(139, 92, 246, 0.3);
}

/* Footer */
.privacy-footer {
    text-align: center;
    padding: 2rem;
    color: var(--text-secondary);
    font-style: italic;
    border-top: 1px solid var(--border-color);
}

/* Responsive Design */
@media (max-width: 768px) {
    .privacy-policy {
        padding: 1rem 0;
        margin-top: 70px;
    }
    
    .privacy-policy .container {
        padding: 0 1rem;
    }
    
    .page-title {
        font-size: 2.5rem;
    }
    
    .page-description {
        font-size: 1.1rem;
    }
    
    .privacy-section {
        padding: 1.5rem;
    }
    
    .privacy-section h2 {
        font-size: 1.5rem;
    }
    
    .policy-item h3,
    .policy-item h4 {
        font-size: 1.2rem;
    }
}

@media (max-width: 480px) {
    .page-title {
        font-size: 2rem;
    }
    
    .privacy-section {
        padding: 1rem;
    }
    
    .app-header {
        padding: 1rem;
    }
    
    .contact-link {
        padding: 0.75rem 1.5rem;
        font-size: 0.9rem;
    }
}
