/* Case Studies Section */
.case-studies {
    padding: 100px 0;
    background: linear-gradient(135deg, var(--bg-primary) 0%, var(--bg-secondary) 100%);
    position: relative;
    overflow: hidden;
}

.case-studies::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="grid" width="10" height="10" patternUnits="userSpaceOnUse"><path d="M 10 0 L 0 0 0 10" fill="none" stroke="rgba(118, 76, 164, 0.1)" stroke-width="0.5"/></pattern></defs><rect width="100" height="100" fill="url(%23grid)"/></svg>');
    opacity: 0.3;
    z-index: 0;
}

.case-studies .container {
    position: relative;
    z-index: 1;
}

.case-studies__header {
    text-align: center;
    margin-bottom: 60px;
}

.case-studies__header .section-badge {
    display: inline-block;
    padding: 8px 20px;
    background: var(--gradient-subtle);
    color: var(--accent-primary);
    border: 1px solid var(--border-color);
    border-radius: 50px;
    font-size: 14px;
    font-weight: 600;
    margin-bottom: 20px;
    backdrop-filter: blur(10px);
}

.case-studies__header .section-title {
    font-size: clamp(2.5rem, 5vw, 3.5rem);
    font-weight: 700;
    color: var(--text-primary);
    margin-bottom: 20px;
    line-height: 1.2;
}

.case-studies__header .title-gradient {
    background: linear-gradient(135deg, var(--accent-primary), var(--accent-secondary));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.case-studies__header .section-description {
    font-size: 1.1rem;
    color: var(--text-secondary);
    max-width: 600px;
    margin: 0 auto;
    line-height: 1.6;
}

/* Filters */
.case-studies__filters {
    display: flex;
    justify-content: center;
    gap: 15px;
    margin-bottom: 50px;
    flex-wrap: wrap;
}

.filter-btn {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 12px 24px;
    background: var(--bg-card);
    border: 1px solid var(--border-color);
    border-radius: 50px;
    color: var(--text-secondary);
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.3s ease;
    backdrop-filter: blur(10px);
}

.filter-btn:hover {
    background: var(--bg-card-light);
    border-color: var(--border-color);
    color: var(--accent-primary);
    transform: translateY(-2px);
}

.filter-btn.active {
    background: var(--gradient-primary);
    border-color: transparent;
    color: white;
    box-shadow: 0 8px 32px var(--shadow-accent);
}

.filter-btn svg {
    font-size: 16px;
}

/* Case Studies Grid */
.case-studies__grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(450px, 1fr));
    gap: 30px;
    margin-bottom: 80px;
}

.case-study-card {
    background: var(--bg-card-light);
    border: 1px solid var(--border-color);
    border-radius: 20px;
    overflow: hidden;
    transition: all 0.3s ease;
    backdrop-filter: blur(20px);
    position: relative;
}

.case-study-card:hover {
    background: var(--bg-card-lighter);
    border-color: var(--border-color);
    box-shadow: 0 20px 60px var(--shadow-dark);
}

/* Compact Card Styles */
.case-study-card.compact {
    min-height: 400px;
    cursor: pointer;
}

.case-study-card.compact .case-study-content {
    padding: 1.5rem;
}

.case-study-card.compact .case-study-title {
    font-size: 1.3rem;
    margin-bottom: 1rem;
    line-height: 1.3;
}

.case-study-card.compact .case-study-description {
    font-size: 0.95rem;
    color: var(--text-secondary);
    margin-bottom: 1.5rem;
    line-height: 1.5;
}

.case-study-card.compact .tech-stack {
    display: flex;
    gap: 0.75rem;
    align-items: center;
    margin-bottom: 1rem;
}

.case-study-card.compact .tech-icon {
    font-size: 1.5rem;
    transition: transform 0.3s ease;
}

.case-study-card.compact .tech-icon:hover {
    transform: scale(1.2);
}

.case-study-card.compact .tech-more {
    background: var(--bg-card-lighter);
    border: 1px solid var(--border-color);
    border-radius: 50%;
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0.8rem;
    color: var(--text-secondary);
}

.case-study-card.compact .metric-item {
    display: flex;
    flex-direction: column;
    gap: 0.25rem;
}

.case-study-card.compact .metric-label {
    font-size: 0.8rem;
    color: var(--text-secondary);
    font-weight: 500;
}

.case-study-card.compact .metric-value {
    font-size: 0.9rem;
    color: var(--accent-primary);
    font-weight: 600;
}

.overlay-button {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    background: rgba(255, 255, 255, 0.9);
    color: var(--bg-primary);
    padding: 0.75rem 1.5rem;
    border-radius: 25px;
    font-weight: 600;
    font-size: 0.9rem;
    backdrop-filter: blur(10px);
    transition: all 0.3s ease;
}

.overlay-button:hover {
    background: white;
    transform: scale(1.05);
}

/* Case Study Image */
.case-study-image {
    position: relative;
    height: 200px;
    background: linear-gradient(135deg, var(--accent-primary), var(--accent-secondary));
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    background-color: var(--bg-secondary); /* Fallback background */
    box-shadow: inset 0 0 30px rgba(0, 0, 0, 0.3);
    filter: contrast(1.05);
}

.image-placeholder {
    font-size: 4rem;
    color: rgba(255, 255, 255, 0.8);
    z-index: 1;
}

.case-study-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.8);
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transition: all 0.3s ease;
    z-index: 2;
}

.case-study-card:hover .case-study-overlay {
    opacity: 1;
}

.overlay-buttons {
    display: flex;
    gap: 15px;
}

.overlay-btn {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 10px 20px;
    background: rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 30px;
    color: white;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.3s ease;
    backdrop-filter: blur(10px);
}

.overlay-btn:hover {
    background: rgba(255, 255, 255, 0.2);
    transform: translateY(-2px);
}

/* Case Study Content */
.case-study-content {
    padding: 30px;
}

.case-study-header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 25px;
    gap: 15px;
}

.case-study-title {
    font-size: 1.4rem;
    font-weight: 600;
    color: var(--text-primary);
    line-height: 1.3;
    flex: 1;
}

.case-study-category {
    padding: 4px 12px;
    background: rgba(139, 92, 246, 0.1);
    color: var(--accent-primary);
    border: 1px solid rgba(139, 92, 246, 0.3);
    border-radius: 20px;
    font-size: 12px;
    font-weight: 500;
    white-space: nowrap;
}

.case-study-challenge,
.case-study-solution,
.case-study-technologies,
.case-study-features,
.case-study-metrics {
    margin-bottom: 25px;
}

.case-study-challenge h4,
.case-study-solution h4,
.case-study-technologies h4,
.case-study-features h4,
.case-study-metrics h4 {
    font-size: 1rem;
    font-weight: 600;
    color: var(--text-primary);
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    gap: 8px;
}

.case-study-challenge h4::before {
    content: '🎯';
}

.case-study-solution h4::before {
    content: '💡';
}

.case-study-technologies h4::before {
    content: '⚡';
}

.case-study-features h4::before {
    content: '✨';
}

.case-study-metrics h4::before {
    content: '📊';
}

.case-study-challenge p,
.case-study-solution p {
    color: var(--text-secondary);
    line-height: 1.6;
    font-size: 0.95rem;
}

/* Technologies */
.tech-stack {
    display: flex;
    flex-wrap: wrap;
    gap: 12px;
}

.tech-item {
    display: flex;
    align-items: center;
    gap: 6px;
    padding: 6px 12px;
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 20px;
    color: var(--text-secondary);
    font-size: 0.85rem;
    font-weight: 500;
    transition: all 0.3s ease;
}

.tech-item:hover {
    background: rgba(255, 255, 255, 0.1);
    transform: translateY(-2px);
}

.tech-item svg {
    font-size: 14px;
}

/* Features */
.case-study-features ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

.case-study-features li {
    color: var(--text-secondary);
    font-size: 0.9rem;
    line-height: 1.5;
    margin-bottom: 8px;
    padding-left: 20px;
    position: relative;
}

.case-study-features li::before {
    content: '✓';
    position: absolute;
    left: 0;
    color: var(--accent-primary);
    font-weight: bold;
}

/* Metrics */
.metrics-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
    gap: 15px;
}

.metric-item {
    padding: 12px;
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 12px;
    text-align: center;
    transition: all 0.3s ease;
}

.metric-item:hover {
    background: rgba(139, 92, 246, 0.1);
    border-color: rgba(139, 92, 246, 0.3);
}

.metric-label {
    display: block;
    font-size: 0.8rem;
    color: var(--text-secondary);
    margin-bottom: 4px;
}

.metric-value {
    display: block;
    font-size: 0.9rem;
    font-weight: 600;
    color: var(--accent-primary);
}

/* Call to Action */
.case-studies__cta {
    text-align: center;
    padding: 60px 40px;
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 20px;
    backdrop-filter: blur(20px);
}

.case-studies__cta h3 {
    font-size: 2rem;
    font-weight: 700;
    color: var(--text-primary);
    margin-bottom: 15px;
}

.case-studies__cta p {
    font-size: 1.1rem;
    color: var(--text-secondary);
    margin-bottom: 30px;
    max-width: 500px;
    margin-left: auto;
    margin-right: auto;
}

.cta-button {
    padding: 15px 30px;
    background: linear-gradient(135deg, var(--accent-primary), var(--accent-secondary));
    border: none;
    border-radius: 50px;
    color: white;
    font-size: 1rem;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
    box-shadow: 0 8px 32px rgba(139, 92, 246, 0.3);
}

.cta-button:hover {
    box-shadow: 0 12px 40px rgba(139, 92, 246, 0.4);
    transform: translateY(-2px);
}

/* Modal Styles */
.case-study-modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.8);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    padding: 2rem;
    backdrop-filter: blur(8px);
}

.case-study-modal {
    background: var(--bg-card-light);
    border-radius: 20px;
    max-width: 1200px;
    width: 100%;
    max-height: 90vh;
    overflow-y: auto;
    position: relative;
    border: 1px solid var(--border-color);
    box-shadow: 0 25px 50px rgba(0, 0, 0, 0.5);
}

.modal-close {
    position: absolute;
    top: 1.5rem;
    right: 1.5rem;
    background: var(--bg-card);
    border: 1px solid var(--border-color);
    border-radius: 50%;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--text-primary);
    cursor: pointer;
    transition: all 0.3s ease;
    z-index: 10;
}

.modal-close:hover {
    background: var(--bg-card-lighter);
    transform: scale(1.1);
}

.modal-content {
    padding: 2rem;
}

.modal-header {
    margin-bottom: 2rem;
}

.modal-header h2 {
    font-size: 2rem;
    color: var(--text-primary);
    margin-bottom: 0.5rem;
}

.modal-category {
    color: var(--accent-primary);
    font-weight: 600;
    font-size: 1rem;
}

.modal-gallery {
    margin-bottom: 2rem;
}

.gallery-main {
    position: relative;
    border-radius: 12px;
    overflow: hidden;
    margin-bottom: 1rem;
    width: 800px;
    height: 800px;
    max-width: 100%;
    max-height: 80vh;
    margin-left: auto;
    margin-right: auto;
    background: #181c2f; /* subtle dark background for letterboxing */
    display: flex;
    align-items: center;
    justify-content: center;
}

.gallery-main img {
    width: 100%;
    height: 100%;
    object-fit: contain; /* show full image, no cropping */
    background: transparent;
}

.gallery-nav {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background: rgba(0, 0, 0, 0.7);
    border: none;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    cursor: pointer;
    transition: all 0.3s ease;
    backdrop-filter: blur(10px);
}

.gallery-nav:hover {
    background: rgba(0, 0, 0, 0.9);
    transform: translateY(-50%) scale(1.1);
}

.gallery-nav.prev {
    left: 1rem;
}

.gallery-nav.next {
    right: 1rem;
}

.gallery-thumbnails {
    display: flex;
    gap: 1rem;
    justify-content: center;
}

.gallery-thumbnails img {
    width: 80px;
    height: 60px;
    object-fit: cover;
    border-radius: 8px;
    cursor: pointer;
    border: 2px solid transparent;
    transition: all 0.3s ease;
}

.gallery-thumbnails img:hover {
    border-color: var(--accent-light);
}

.gallery-thumbnails img.active {
    border-color: var(--accent-primary);
}

.modal-details {
    display: grid;
    gap: 2rem;
}

.modal-section h4 {
    color: var(--text-primary);
    font-size: 1.2rem;
    margin-bottom: 1rem;
}

.modal-section p {
    color: var(--text-secondary);
    line-height: 1.6;
}

.modal-tech-stack {
    display: flex;
    flex-wrap: wrap;
    gap: 0.75rem;
}

.modal-tech-badge {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    padding: 0.5rem 1rem;
    border: 1px solid;
    border-radius: 25px;
    font-size: 0.9rem;
    font-weight: 500;
    color: var(--text-primary);
}

.modal-features {
    list-style: none;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 0.5rem;
}

.modal-features li {
    padding: 0.5rem 0;
    padding-left: 1.5rem;
    color: var(--text-secondary);
    position: relative;
}

.modal-features li::before {
    content: '✓';
    position: absolute;
    left: 0;
    color: var(--accent-primary);
    font-weight: bold;
}

.modal-metrics {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 1rem;
}

.modal-metric-item {
    background: var(--bg-card);
    padding: 1rem;
    border-radius: 12px;
    border: 1px solid var(--border-color);
    text-align: center;
}

.modal-metric-label {
    display: block;
    font-size: 0.9rem;
    color: var(--text-secondary);
    margin-bottom: 0.5rem;
}

.modal-metric-value {
    display: block;
    font-size: 1.1rem;
    font-weight: 700;
    color: var(--accent-primary);
}

.modal-actions {
    display: flex;
    gap: 1rem;
    justify-content: center;
    margin-top: 2rem;
}

.modal-btn {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    padding: 0.75rem 1.5rem;
    border-radius: 25px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
    text-decoration: none;
    border: none;
}

.modal-btn.demo {
    background: var(--gradient-primary);
    color: white;
}

.modal-btn.demo:hover {
    transform: translateY(-2px);
    box-shadow: 0 8px 25px var(--shadow-accent);
}

.modal-btn.code {
    background: var(--bg-card);
    color: var(--text-primary);
    border: 1px solid var(--border-color);
}

.modal-btn.code:hover {
    background: var(--bg-card-lighter);
    transform: translateY(-2px);
}

/* Responsive Design */
@media (max-width: 768px) {
    .case-studies {
        padding: 60px 0;
    }

    .case-studies__grid {
        grid-template-columns: 1fr;
        gap: 20px;
        margin-bottom: 60px;
    }

    .case-study-card {
        margin: 0 10px;
    }

    .case-study-content {
        padding: 20px;
    }

    .case-study-header {
        flex-direction: column;
        align-items: flex-start;
        gap: 10px;
    }

    .case-study-category {
        align-self: flex-start;
    }

    .case-studies__filters {
        gap: 10px;
        margin: 0 20px 40px 20px;
    }

    .filter-btn {
        padding: 10px 16px;
        font-size: 13px;
    }

    .filter-btn span {
        display: none;
    }

    .metrics-grid {
        grid-template-columns: repeat(2, 1fr);
        gap: 10px;
    }

    .case-studies__cta {
        margin: 0 20px;
        padding: 40px 20px;
    }

    .case-studies__cta h3 {
        font-size: 1.5rem;
    }

    .overlay-buttons {
        flex-direction: column;
        gap: 10px;
    }

    .tech-stack {
        gap: 8px;
    }

    .tech-item {
        padding: 4px 8px;
        font-size: 0.8rem;
    }
}

@media (max-width: 480px) {
    .case-studies__header .section-title {
        font-size: 2rem;
    }

    .case-study-image {
        height: 150px;
    }

    .image-placeholder {
        font-size: 3rem;
    }

    .case-study-title {
        font-size: 1.2rem;
    }

    .metrics-grid {
        grid-template-columns: 1fr;
    }
}

/* Mobile Responsiveness for Modal */
@media (max-width: 768px) {
    .case-study-modal-overlay {
        padding: 1rem;
    }
    
    .modal-content {
        padding: 1.5rem;
    }
    
    .modal-header h2 {
        font-size: 1.5rem;
    }
    
    .gallery-nav {
        width: 40px;
        height: 40px;
    }
    
    .gallery-thumbnails {
        gap: 0.5rem;
    }
    
    .gallery-thumbnails img {
        width: 60px;
        height: 45px;
    }
    
    .modal-features {
        grid-template-columns: 1fr;
    }
    
    .modal-metrics {
        grid-template-columns: 1fr;
    }
    
    .modal-actions {
        flex-direction: column;
    }
}
