/* Global Styles */
@import './image-enhancements.css';

:root {
  /* Colors - Beautiful Purple-Blue Gradient Theme */
  --bg-primary: #1a0d2e;        /* Very dark purple base */
  --bg-secondary: #2d1b4e;      /* Dark purple-blue */
  --bg-card: #3d2a5c;           /* Medium purple for cards */
  --bg-card-light: #5a4080;     /* Lighter purple for cards */
  --bg-card-lighter: #6b4d96;   /* Even lighter purple for featured cards */
  --bg-card-lightest: #7c5aac;  /* Lightest purple for highlighted cards */
  --text-primary: #f8fafc;      /* Pure white text */
  --text-secondary: #cbd5e1;    /* Light purple-tinted text */
  --text-color: #e2e8f0;        /* Main text color */
  --accent-primary: #764ca4;    /* Main purple from hero */
  --accent-secondary: #667de9;  /* Main blue from hero */
  --accent-light: #9b7bc7;      /* Lighter purple */
  --accent-lighter: #8fa8f0;    /* Lighter blue */
  --gradient-primary: linear-gradient(135deg, #764ca4 0%, #667de9 100%);
  --gradient-secondary: linear-gradient(135deg, #9b7bc7 0%, #8fa8f0 100%);
  --gradient-subtle: linear-gradient(135deg, rgba(118, 76, 164, 0.1) 0%, rgba(102, 125, 233, 0.1) 100%);
  --border-color: rgba(118, 76, 164, 0.2);
  --shadow-dark: rgba(0, 0, 0, 0.4);
  --shadow-accent: rgba(118, 76, 164, 0.3);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
  line-height: 1.6;
  color: #e4e4e7;
  background-color: #0f0f23;
}

.App {
  overflow-x: hidden;
}

/* Typography */
h1, h2, h3, h4, h5, h6 {
  font-weight: 700;
  line-height: 1.2;
  margin-bottom: 1rem;
}

h1 {
  font-size: 3.5rem;
  font-weight: 800;
}

h2 {
  font-size: 2.5rem;
  font-weight: 700;
}

h3 {
  font-size: 2rem;
  font-weight: 600;
}

p {
  margin-bottom: 1rem;
  line-height: 1.7;
}

/* Container */
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.container-fluid {
  width: 100%;
  padding: 0 20px;
}

/* Sections */
section {
  padding: 100px 0;
}

.section-title {
  text-align: center;
  margin-bottom: 3rem;
}

.section-title h2 {
  font-size: 2.5rem;
  font-weight: 700;
  color: #f4f4f5;
  margin-bottom: 1rem;
  position: relative;
}

.section-title h2::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 60px;
  height: 3px;
  background: var(--gradient-primary);
  border-radius: 2px;
}

.section-title p {
  color: #a1a1aa;
  font-size: 1.1rem;
  max-width: 600px;
  margin: 0 auto;
}

/* Buttons */
.btn {
  display: inline-block;
  padding: 15px 30px;
  font-size: 1rem;
  font-weight: 600;
  text-decoration: none;
  border-radius: 50px;
  transition: all 0.3s ease;
  cursor: pointer;
  border: none;
  text-align: center;
}

.btn-primary {
  background: var(--gradient-primary);
  color: white;
  box-shadow: 0 10px 30px var(--shadow-accent);
}

.btn-primary:hover {
  transform: translateY(-3px);
  box-shadow: 0 15px 40px var(--shadow-accent);
}

.btn-outline {
  background: transparent;
  color: var(--accent-primary);
  border: 2px solid var(--accent-primary);
}

.btn-outline:hover {
  background: var(--accent-primary);
  color: white;
  transform: translateY(-3px);
}

.btn-white {
  background: var(--bg-card);
  color: var(--text-primary);
  box-shadow: 0 10px 30px var(--shadow-dark);
}

.btn-white:hover {
  transform: translateY(-3px);
  box-shadow: 0 15px 40px var(--shadow-dark);
}

/* Cards */
.card {
  background: var(--bg-card);
  border-radius: 15px;
  padding: 2rem;
  box-shadow: 0 10px 40px var(--shadow-dark);
  transition: all 0.3s ease;
  border: 1px solid var(--border-color);
}

.card:hover {
  transform: translateY(-10px);
  box-shadow: 0 20px 60px var(--shadow-dark);
  border-color: var(--accent-light);
}

/* Grid System */
.row {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -15px;
}

.col-12 {
  flex: 0 0 100%;
  max-width: 100%;
  padding: 0 15px;
}

.col-6 {
  flex: 0 0 50%;
  max-width: 50%;
  padding: 0 15px;
}

.col-4 {
  flex: 0 0 33.333333%;
  max-width: 33.333333%;
  padding: 0 15px;
}

.col-3 {
  flex: 0 0 25%;
  max-width: 25%;
  padding: 0 15px;
}

/* Utilities */
.text-center {
  text-align: center;
}

.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}

.mb-4 {
  margin-bottom: 2rem;
}

.mb-5 {
  margin-bottom: 3rem;
}

.mt-4 {
  margin-top: 2rem;
}

.mt-5 {
  margin-top: 3rem;
}

/* Background Gradients */
.gradient-bg {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}

.gradient-text {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/* Responsive Design */
@media (max-width: 1200px) {
  .container {
    max-width: 960px;
  }
  
  h1 {
    font-size: 3rem;
  }
  
  h2 {
    font-size: 2.2rem;
  }
}

@media (max-width: 992px) {
  .container {
    max-width: 720px;
  }
  
  .col-6 {
    flex: 0 0 100%;
    max-width: 100%;
    margin-bottom: 2rem;
  }
  
  .col-4 {
    flex: 0 0 50%;
    max-width: 50%;
    margin-bottom: 2rem;
  }
  
  h1 {
    font-size: 2.5rem;
  }
  
  section {
    padding: 80px 0;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: 540px;
  }
  
  .col-4, .col-6 {
    flex: 0 0 100%;
    max-width: 100%;
    margin-bottom: 2rem;
  }
  
  h1 {
    font-size: 2.2rem;
  }
  
  h2 {
    font-size: 1.8rem;
  }
  
  .btn {
    padding: 12px 25px;
    font-size: 0.9rem;
  }
  
  section {
    padding: 60px 0;
  }
}

@media (max-width: 576px) {
  .container, .container-fluid {
    padding: 0 15px;
  }
  
  h1 {
    font-size: 1.8rem;
  }
  
  .section-title h2 {
    font-size: 1.6rem;
  }
  
  .card {
    padding: 1.5rem;
  }
  
  section {
    padding: 50px 0;
  }
}